body {
  margin: 0;
  padding: 0;
}

.topBar {
  height: 60px;
  background-color: #0062ff;
  justify-content: center;
  align-items: center;
  gap: 0;
  margin: 0;
  display: flex;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .35);
}

.formField {
  display: flex;
  position: relative;
}

.search-imput {
  width: 100%;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 20px 0 0 20px;
  outline: 0;
  padding: 10px;
}

.search-button {
  height: 45px;
  width: 25%;
  cursor: pointer;
  border: none;
  border-radius: 0 20px 20px 0;
}

.search-button:active {
  background-color: #c4c2c2;
  scale: 1.05;
}

.icon {
  width: 10px;
  height: 10px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

